<template>
  <div>
    <div class="bg" v-if="this.is_weixin()">
      <SignIn v-show="hasLogin" :isLogin="login"></SignIn>
      <div class="pmd">
        <PaoMaDen/>
      </div>

      <div class="content">
        <div class="gift-detil" v-if="!successPageShow">
          <div class="gift"
               :style="{ 'background-image': 'url(' + giftDetail.img + ')','background-repeat':'no-repeat','background-size':'cover' }"></div>
          <div class="name">{{giftDetail.name}}</div>
          <div class="dis ellipsis">
            {{giftDetail.des}}
          </div>
          <div class="title">
            <div class="line" style="width:100%"></div>
          </div>
          <p v-show="giftDetail.status == 0">
            还差{{giftDetail.needScore}}战力值就能夺得宝物哦！
          </p>
          <div class="progress-bar">
            <div class="left icon"></div>
            <div class="bar">
              <div :style="progress"></div>
            </div>
            <div class="right icon"></div>
          </div>
          <div :class="successPageShow?'hide':'show'">
            <div class="time">
              剩余夺宝时间:<span>{{giftDetail.time.d}}</span>天<span>{{giftDetail.time.h}}</span>时<span>{{giftDetail.time.m}}</span>分<span>{{giftDetail.time.s}}</span>秒
            </div>
            <div ref="getIng" :class="infoComplete?'hide':'show'">
              <div class="find-more find" v-show="giftDetail.status !== 1 || giftDetail.userId !== userId">
                <a v-on:click="findMore">
                  更多宝藏
                </a>
              </div>
              <div class="find-help find" v-show="giftDetail.status == 0 && giftDetail.userId == userId"
                   v-on:click="getHelp">
                喊人寻宝
              </div>
              <div v-show="giftDetail.status == 2 && giftDetail.userId == userId" class="find-help find time-out">
                已超时
              </div>
              <div v-show="giftDetail.status == 4 && giftDetail.userId == userId" class="find-help find time-out">
                已取消
              </div>
              <div v-show="giftDetail.status == 5 && giftDetail.userId == userId" class="find-help find time-out">
                发货失败
              </div>
              <div class="find-help find" v-show="giftDetail.userId != userId" v-on:click="findMore">
                我也来一个
              </div>
            </div>
            <div class="get-gift" v-show="giftDetail.status == 1 && giftDetail.userId == userId" v-on:click="takeGift">
              去领奖
            </div>
            <!--信息填写 -->
          </div>

          <div class="cancel" v-on:click="cancel" v-if="giftDetail.status == 0 && giftDetail.userId == userId"></div>
        </div>
        <!--领奖页面-->
        <div class="take-gift" v-if="successPageShow">
          <div class="gift-info">
            <div class="head"
                 :style="{ 'background-image': 'url(' + user.img + ')','background-repeat':'no-repeat','background-size':'cover' }"></div>
            <div class="user">
              {{user.name}}
            </div>
            <div class="gift-content">
              <div class="gift"
                   :style="{ 'background-image': 'url(' + giftDetail.img + ')','background-repeat':'no-repeat','background-size':'cover' }"></div>
              <div class="gift-right">
                <div class="dis ellipsis">
                  {{giftDetail.name}},{{giftDetail.des}}
                </div>
              </div>
            </div>
            <div class="title">
              <div class="line" style="width:100%"></div>
            </div>
            <!--填写信息-->
            <div class="info">
              <div class="name" v-if="giftDetail.type == 0">
                <span>收件人:</span>
                <input v-model="info.name" type="text" :disabled="infoComplete?true:false" placeholder="姓名">
              </div>
              <p style="height:0.4rem;text-align: center;color:#000" v-if="giftDetail.type == 1">{{giftDetail.type ==
                1?'密钥:':''}}</p>
              <div class="name" v-if="giftDetail.type !== 1">
                <span>{{giftDetail.type == 0?'联系电话:':giftDetail.type == 2?'充值账号:':''}}</span>
                <input v-if="giftDetail.type !== 1" v-model="info.phone" type="text" :disabled="infoComplete?true:false"
                       :placeholder="giftDetail.type == 0?'联系电话':giftDetail.type == 2?'充值账号':''">
              </div>
              <p style="text-align: center;width:100%;font-size: .28rem;" v-if="giftDetail.type == 1">
                {{secretKeyText}}</p>
              <div class="name" v-show="giftDetail.type == 0">
                <span>收货地址:</span>
                <input v-model="info.address" type="text" :disabled="infoComplete?true:false" placeholder="收货地址">
              </div>
              <p style="height:0.4rem" v-if="giftDetail.type == 2"></p>
              <p>{{successPageShow == true?'':'注:收货信息一旦提交则不更改,请仔细填写!'}}</p>
              <div class="submit" v-if="!infoComplete" v-on:click="submit(giftDetail.id)">提交</div>
              <p class="more-gift" v-if="infoComplete" v-on:click="findMore">
                更多宝藏
              </p>
            </div>
          </div>
        </div>
        <!--夺宝小分队-->
        <div class="xiaofendui" v-if="helpList.length">
          <div class="title">
            <div class="line"></div>
            <span>寻宝小分队</span>
            <div class="line"></div>
          </div>
          <div class="help-list" id="scrollbar" @scroll="handleScroll()">
            <div id="scroll">
              <div class="item" v-for="(item,key) in helpList">
                <img :src="item.userEntity.headimgurl" class="head-img" alt="">
                <div class="wx-name">{{item.userEntity.nickname}}</div>
                <div class="grade">
                  +{{item.score}}
                </div>
              </div>
            </div>
          </div>
        </div>
        <!--夺宝记录-->
        <div class="gift-record" v-if="myRecord.length != 0">
          <div class="title">
            <div class="line"></div>
            <span>我的记录</span>
            <div class="line"></div>
          </div>
          <div style="max-height:13rem;overflow: auto" id="r-scrollbar" @scroll="handleScroll('record')">
            <div id="r-scroll">
              <ul class="my-record">
                <li v-for="(item,key) in myRecord" class="item" v-on:click="queryWinGoodsById(item.id,key) "
                    :class="item.status == 0?'nowget':item.status == 1?'success':item.status == 2?'file':item.status == 3?'send':item.status == 4?'cancle':item.status == 5?'send-fail':''">
                  <div class="left">
                    <div
                      :style="{ 'background-image': 'url(' + item.goodsEntity.picResUrl + ')','background-repeat':'no-repeat','background-size':'cover' }"></div>
                  </div>
                  <div class="right">
                    <div class="dis ellipsis">{{item.goodsEntity.name}},{{item.goodsEntity.description}}</div>
                    <div class="count">
                    </div>
                    <div class="right-bottom">
                      <div class="price">
                      </div>
                      <div class="go" v-show="key != showDetilButton">
                        查看详情
                      </div>
                    </div>
                  </div>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
      <div class="share" ref="share">
        <img :src="shareList[nowShareImg]" alt="">
        <div class="change-img">
          <h5>点击菜单<span style="color:red">「热门文章」</span>，邀请好友在文章内<span style="color:red;">留言回复四位暗号</span>，为你助力！</h5>
          <!--<div class="button" v-on:click="changeImg">换一张</div>-->
        </div>
      </div>
      <div class="full-box" v-show="isSure">
        <div class="sure">
          <p>是否取消领取该宝物</p>
          <div v-on:click="sure('y')">确定</div>
          <div v-on:click="sure('n')">取消</div>
        </div>
      </div>
      <Loading v-show="loading"/>
    </div>
    <div v-if="!this.is_weixin()">
      <p style="font-size:20px;text-align: center;margin-top:3rem;padding-bottom:.1rem">长按识别二维码</p>
      <p style="font-size:20px;text-align: center;margin-bottom:.2rem">关注百度校园公众号</p>
      <img src="../../lib/qrCode.png" alt="">
    </div>
  </div>
</template>

<script>
  import axios from 'axios';
  import PaoMaDen from '@/components/cangbaotu/PaoMaDen.vue'
  import Loading from '@/components/shetuan/Loading.vue'
  import SignIn from '../../lib/SignIn';
  import qs from 'qs'

  export default {
    name: "GiftDetil",
    data() {
      return {
        giftDetail: { //显示的礼品信息
          id: '',
          name: '',
          des: '',
          img: '',
          type: '',
          userId: '',
          needScore: '',
          status: '',
          time: {
            d: '0',
            h: '00',
            m: '00',
            s: '00',
            endTime: '',
            status: '',
          },
        },
        infoComplete: false,//是否填写信息
        successPageShow: false,//是否夺宝成功
        helpList: [],//帮助列表
        myRecord: [],//夺宝记录
        progress: 0,//进度条
        shareList: [],//分享图片列表
        nowShareImg: 0,
        user: {//当前用户
          img: '',
          name: ''
        },
        userId: '',//当前用户id
        successInfo: {//成功以后显示的信息
          phone: ''
        },
        info: {
          address: '',
          phone: '',
          name: ''
        },
        secretKeyText: '',
        flag: true,
        showDetilButton: '',//夺宝记录中查看详情按钮
        scoreName: ['船长', '炮手', '大副', '小喽喽', '船医', '厨子', '小喽喽', '船医', '船长', '小喽喽', '炮手', '厨子', '大副'],
        hasLogin: false,
        isSure: false,
        loading: false,
        getHistory: {
          start: 0,
          limit: 10
        },
        supportHistory: {
          start: 0,
          limit: 6
        }

      }
    },
    mounted() {
      //允许复制
      // let dom = document.getElementById('user-select');
      // document.oncontextmenu = function (e) {
      //   if (e.target !== dom) {
      //     e.preventDefault();
      //   }
      // };
      this.forbidden_wx_share();
      if (this.is_weixin()) {
        //小队成员昵称
        let names = [];
        for (var j = 0; j < 80; j++) {
          for (var i = 0; i < this.scoreName.length; i++) {
            names.push(this.scoreName[i]);
          }
        }
        this.scoreName = names;
        this.loading = true;
        this.initPage();
      } else {
        alert('请在微信内打开')
      }
    },
    methods: {
      async initPage() {
        let getMyself = await this.getMySelf();
        if (getMyself !== undefined) {
          //带id
          this.queryWinGoodsById(getMyself);
        } else {
          //不带id
          this.queryAllWinGoods();
        }
      },
      getMySelf() {
        return new Promise((result, rej) => {
          axios.post(this.proxy + 'queryUser.do', {}).then((res) => {
            if (res.status === 200) {
              if (res.data.error_no == '0') {
                this.userId = res.data.data.id;//用户id
                this.hasLogin = false;
                if (this.getQueryParams('id')) {
                  result(this.getQueryParams('id'))
                } else {
                  result();
                }
              }
              if (res.data.error_no == '2') {
                this.hasLogin = true;
              }
            }
          })
        })
      },
      queryWinGoodsById(id, key) {
        if (key) {
          this.showDetilButton = key;
          this.loading = true;
        }
        //根据id获取信息
        axios.get(this.proxy + 'queryWinGoodsById.do?winId=' + id, {}).then((res) => {
          if (res.status === 200) {
            if (res.data.error_no == '0') {
              if (key) {
                this.loading = false;
              }
              this.pageData(res.data.data, id);//对象形式
            }
          }
        })
      },
      queryAllWinGoods(str) {
        //获取当前用户所有的夺宝记录
        let data = {
          start: this.getHistory.start,
          limit: this.getHistory.limit
        }
        axios.post(this.proxy + 'queryMyWinGoods.do ', qs.stringify(data)).then((res) => {
          if (res.status == 200) {
            if (res.data.error_no == '0') {
              this.getHistory.start += 10;
              this.myRecord = this.myRecord.concat(res.data.data);
              this.flag = false;
              if (str !== 'next') {
                this.pageData(res.data.data);//数组形式
              }
            }
            if (res.data.error_no == '1') {
              alert('没有更多了！')
            }
          }
        })
      },
      pageData: function (data, id) {
        var _that = this;
        var records = [];
        //todo 待优化
        //判断是否为数组(如果夺宝记录只有一条不为数组)
        if (Object.prototype.toString.call(data) == '[object Array]') {
          records = data;
        } else {
          records.push(data);
        }
        //获取正在夺宝，默认为数组的第一个
        let i = 0;
        this.giftDetail.name = records[i].goodsEntity.name;//名称
        this.giftDetail.des = records[i].goodsEntity.description;//描述
        this.giftDetail.img = records[i].goodsEntity.picResUrl;//图片
        this.giftDetail.status = records[i].status;//状态
        this.giftDetail.id = records[i].id;//礼品id
        this.giftDetail.userId = records[i].userId;//用户id
        this.giftDetail.type = records[i].goodsEntity.goodsType;//商品类型
        if (records[i].supportScoresTotal) {
          records[i].supportScoresTotal = records[i].supportScoresTotal
        } else {
          records[i].supportScoresTotal = 0
        }
        this.giftDetail.needScore = records[i].scores - records[i].supportScoresTotal;//需要分数
        if (this.giftDetail.needScore < 0) {
          this.giftDetail.needScore = 0;
        }
        if (records[i].supportScoresTotal > records[i].scores) {
          this.progress = 'width:100%';//进度条
        } else {
          this.progress = 'width:' + records[i].supportScoresTotal / records[i].scores * 100 + '%';//进度条
        }
        if (this.giftDetail.needScore < 0) {
          this.giftDetail.needScore = 0;
        }
        this.giftDetail.time.endTime = records[i].endTime;//结束时间
        //用户
        this.user.img = records[i].userEntity.headimgurl//头像
        this.user.name = records[i].userEntity.nickname//昵称
        if (records[i].shareUrlsList !== undefined) { //分享图片，图片可能为undefined
          this.shareList = records[i].shareUrlsList;
        }
        // 初始化
        this.infoComplete = false
        this.successPageShow = false
        //判断是不是本人
        if (this.giftDetail.userId !== this.userId) {
          this.myRecord = [];
        } else {
          if (this.getQueryParams('id') !== null) {
            this.queryAllWinGoods('next')
          }
          /**
           * 0:实物 1 ：密钥或密钥图片  2 充值卡
           */
          /**
           * 0：抢购中 1 完成 2 失败 3 已发货 4 取消藏宝图 5 发货失败
           */
          if (records[i].status == 1) {
            //判断是否填写过信息
            //判断物品类型
            if (_that.giftDetail.type == 2 || _that.giftDetail.type == 0) {
              axios.get(_that.proxy + 'queryProvideInfo.do?winId=' + _that.giftDetail.id, {}).then(function (res) {
                //未完成信息填写
                if (res.data.error_no == 1 && res.data.data.code == '100008') {
                  _that.infoComplete = false
                  _that.info.name = '';
                  _that.info.address = '';
                  _that.info.phone = '';
                  alert(res.data.data.message);
                }
                //已完成信息填写
                if (res.data.error_no == 0) {
                  _that.info.name = res.data.data.name;
                  _that.info.phone = res.data.data.phone;
                  _that.info.address = res.data.data.address;
                  _that.successPageShow = true
                  _that.infoComplete = true
                }
              })
            }
          }
          // 状态3已经发货
          if (records[i].status == 3) {
            if (_that.giftDetail.type == 1) {
              //如果是卡密去查询卡密接口
              var params = new URLSearchParams();
              axios.post(_that.proxy + 'queryWinCode.do?winId=' + _that.giftDetail.id, {}).then(function (res) {
                if (res.data.error_no == 0) {
                  _that.successPageShow = true;
                  _that.infoComplete = true;
                  _that.secretKeyText = res.data.data.secretKeyText;
                }
                if (res.data.error_no == 1) {
                  _that.secretKeyText = '';
                  alert(res.data.data.message)
                }
              })
            }
            //如果不是卡密，查询是否发货
            if (_that.giftDetail.type == 0 || _that.giftDetail.type == 2) {
              axios.get(_that.proxy + 'queryProvideInfo.do?winId=' + _that.giftDetail.id, {}).then(function (res) {
                if (res.data.error_no == 1 && res.data.data.code == '100008') {
                  _that.infoComplete = false
                  _that.info.name = '';
                  _that.info.address = '';
                  _that.info.phone = '';
                  alert(res.data.data.message);
                }
                if (res.data.error_no == 0) {
                  _that.infoComplete = true
                  _that.info.name = res.data.data.name;
                  _that.info.phone = res.data.data.phone;
                  _that.info.address = res.data.data.address;
                  _that.successPageShow = true
                }
              })
            }
          }
        }
        if (id == undefined) {
          id = records[i].id;
        }
        //小分队
        if (records[i].supportScoresTotal == '0') {
          this.helpList = [];
        } else {
          this.loadmore_help('init');
          // axios.get(_that.proxy + 'querySupportHistory.do?winId=' + id + '&start=0' + '&limit=' + _that.supportLimit, {}).then((res) => {
          //   if (res.status == 200 && res.data.error_no == '0') {
          //     this.moreSupport = this.supportLimit;
          //     this.helpList = res.data.data.datas;
          //   }
          // })
        }
        _that.countdown();
        _that.loading = false;
      },
      handleScroll(str) {
        // console.log(window.scrollY+window.innerHeight,document.body.scrollHeight)
        if (str) {
          let R_scrollbar = document.getElementById('r-scrollbar');
          let R_scroll = document.getElementById('r-scroll');
          if (R_scrollbar.scrollTop + R_scrollbar.offsetHeight >= R_scroll.scrollHeight) {
            this.queryAllWinGoods('next');
          }
        } else {
          let scrollbar = document.getElementById('scrollbar');
          let scroll = document.getElementById('scroll');
          if (scrollbar.scrollTop + scrollbar.offsetHeight >= scroll.scrollHeight) {
            this.loadmore_help();
          }
        }
      },
      countdown: function () {
        var currentTime = Date.parse(new Date());
        if (this.giftDetail.status !== 0) {
          this.giftDetail.time.d = '0'
          this.giftDetail.time.h = '00'
          this.giftDetail.time.m = '00'
          this.giftDetail.time.s = '00'
          return;
        }
        let _this = this.giftDetail.time
        let t = _this.endTime - currentTime;
        _this.d = parseInt(t / 1000 / 60 / 60 / 24)
        _this.h = parseInt(t / 1000 / 60 / 60 % 24)
        _this.m = parseInt(t / 1000 / 60 % 60)
        _this.s = parseInt(t / 1000 % 60)
        _this.h = _this.h > 9 ? _this.h : '0' + _this.h
        _this.m = _this.m > 9 ? _this.m : '0' + _this.m
        _this.s = _this.s > 9 ? _this.s : '0' + _this.s
        setTimeout(() => {
          this.countdown()
        }, 1000)
      },
      loadmore_help(str) {
        let _that = this;
        _that.loading = true;
        if (str == 'init') {
          this.supportHistory.start = 0;
          this.helpList = [];
        }
        let data = {
          winId: this.giftDetail.id,
          start: this.supportHistory.start,
          limit: this.supportHistory.limit
        }
        axios.post(_that.proxy + 'querySupportHistory.do', qs.stringify(data)).then((res) => {
          if (res.status == 200 && res.data.error_no == '0') {
            if (res.data.data.datas.length !== 0) {
              this.supportHistory.start += 6;
              for (var i = 0; i < res.data.data.datas.length; i++) {
                _that.helpList.push(res.data.data.datas[i]);
              }
            }
            _that.loading = false;
          }
        })
      },
      findMore() {
        this.$router.push('/');
      },
      getHelp() {
        this.$refs.share.style.display = 'block'
      },
      changeImg() {
        if (this.nowShareImg >= this.shareList.length - 1) {
          this.nowShareImg = 0;
        } else {
          this.nowShareImg++;
        }
      },
      //立即领奖
      takeGift: function () {
        this.successPageShow = true;
      },
      submit: function (id) {
        this.loading = true;
        let flag = true;
        let data = this.info;
        if (this.giftDetail.type == 0) {
          for (var i in data) {
            if (data[i] == '') {
              flag = false;
            }
          }
        }
        if (this.giftDetail.type == 2) {
          if (this.info.phone == '') {
            flag = false;
          }
        }
        if (flag) {
          //实物
          if (this.giftDetail.type == 0) {
            data = {
              phone: this.info.phone,
              name: this.info.name,
              address: this.info.address,
              winId: id
            }
          }
          //直充
          if (this.giftDetail.type == 2) {
            data = {
              phone: this.info.phone,
              winId: id
            }
          }
          axios.post(this.proxy + 'savePhone.do', qs.stringify(data)).then(res => {
            if (res.status === 200 && res.data.error_no == '0') {
              this.loading = false;
              alert('提交成功!')
              this.infoComplete = true;
            }
            if (res.status === 200 && res.data.error_no == '1') {
              alert(res.data.data.message);
            }
          })
        } else {
          alert('信息不能为空!');
          this.loading = false;
        }
      },
      //取消夺宝
      cancel: function () {
        this.isSure = true;
      },
      sure(str) {
        if (str == 'y') {
          this.loading = true;
          axios.post(this.proxy + 'cancelGoods.do?id=' + this.giftDetail.id).then(res => {
            if (res.status === 200 && res.data.error_no == '0') {
              alert(res.data.data);
              this.$router.push('/')
            } else {
              alert(res.data.data.code);
            }
          })
        }
        this.isSure = false;
      },
      getQueryParams: function (name, url) {
        if (!url) url = location.href
        name = name.replace(/[\[]/, "\\\[").replace(/[\]]/, "\\\]");
        var regexS = "[\\?&]" + name + "=([^&#]*)";
        var regex = new RegExp(regexS);
        var results = regex.exec(url);
        return results == null ? null : results[1];
      },
      login() {
        window.location.href = location.href;
        location.reload();
      }
    },
    components: {
      PaoMaDen,
      SignIn,
      Loading
    }
  }
</script>

